<template>
	<view class="order mar-t-20">
		<view class="order_info">
			<view class="item flex-center">
				<text class="flag bg-blue">起</text>
				<text class="f-28 color-3 mar-l-10">{{info.start_address}}</text>
				<!-- <text class="f-24 color-9 mar-l-10">(中国北京市)</text> -->
			</view>
			<view class="item flex-center mar-t-20">
				<text class="flag bg-green">终</text>
				<text class="f-28 color-3 mar-l-10">{{info.end_address}}</text>
				
				<!-- <text class="f-24 color-9 mar-l-10">(中国北京市)</text> -->
			</view>
			<view class="item flex-lr mar-t-20">
				<view class="flex-center">
					<image src="../static/img/icon_time.png" mode=""></image>
					<text class="f-28 color-3 mar-l-10">{{info.depart_time}}</text>
				</view>
				<text class="f-28 color-3">预估费用：{{info.expense}}</text>
			</view>
			<view class="mar-t-20 mar-b-20" v-if="info.channel_address &&info.channel_address.length>0">
				<view class="flex">
					<text class="f-28 color-3 flex-0">途经地：</text>
					<view class="flex flex-grow flex-wrap">
						<view class="label_item" v-for="(site,i) in info.channel_address" :key='site'>
							<text class="label_full" v-if="labelIndex == i">{{site}}</text>
							<text class="addr_item" @click="showLabel(i)">{{processSite(site)}}</text>
						</view>
					</view>
				</view>
				<text class="f-22 block color-9 mar-t-15">注：点击途径标签查看详细地址</text>
			</view>
			<view class="flex-center mar-t-15">
				<text class="label">{{info.type==10?'载人':'载物'}}</text>
				<text class="label" v-if="info.type == 10">{{info.people_number}}人</text>
				<text class="label" v-if="info.type == 20">{{info.matter_weight}}</text>
				<text class="label" v-if="info.type == 20">{{info.matter_type}}</text>
			</view>
			<view class="f-24 color-9 mar-t-20">备注：{{info.desc || '无'}}</view>
			<view class="btns">
				<slot name="btns"></slot>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'order',
		props:['info'],
		data(){
			return{
				labelIndex:-1
			}
		},
		methods:{
			processSite(name){
				return name.split('/')[3]
			},
			showLabel(i){
				let time = null
				this.labelIndex = i
				time = setTimeout(()=>{
					this.labelIndex = -1
				},1500)
			}
		},

	}
</script>

<style lang="less" scoped>
	.label_item{
		position: relative;
		margin: 5rpx 0;
		.label_full{
			display: inline-block;
			min-width: 50rpx;
			padding: 0 15rpx;
			height: 40rpx;
			line-height: 40rpx;
			background: rgba(#000,0.8);
			border-radius: 8rpx;
			font-size: 24rpx;	
			color: #fff;
			position: absolute;
			bottom: 45rpx;
			word-wrap: normal;
			white-space: nowrap;
			
		}
	}
	.order {
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 13rpx 0rpx rgba(0, 0, 0, 0.1);
		border-radius: 18rpx;

	}
	.order_info {
		padding: 30rpx 20rpx;
		padding-bottom: 40rpx;
		.item {
			image {
				width: 30rpx;
				height: 30rpx;
			}
		}
		.addr_item {
			min-width: 50rpx;
			padding: 0 15rpx;
			height: 40rpx;
			background: #F2F2F2;
			border-radius: 8rpx;
			font-size: 24rpx;
			font-weight: 500;
			color: #333333;
			line-height: 38rpx;
			margin-right: 10rpx;
			text-align: center;
		}
			
		.label{
			min-width: 50rpx;
			padding: 0 15rpx;
			height: 40rpx;
			line-height: 38rpx;
			background: #3FD83B;
			border-radius: 8rpx;
			font-size: 24rpx;
			margin-right: 10rpx;
			color: #fff;
			text-align: center;
		}
	}
	.btns{
		display: flex;
		justify-content: flex-end;
		margin-top: 40rpx;
	}
</style>
